/* ÌöåÏõêÍ∞ÄÏûÖ ÌéòÏù¥ÏßÄ Ï†ÑÏ≤¥ ÎûòÌçº (Î∏îÎûôÌôÄ Ìè¨Ìï®) */

.signup-page-root {
  position: relative;
  /* Ìó§Îçî Ï†úÏô∏Ìïú ÎÇòÎ®∏ÏßÄ ÌôîÎ©¥ ÎÜíÏù¥ÎßåÌÅºÎßå Ï∞®ÏßÄÌïòÍ≥†, Í∑∏ ÏïàÏóêÏÑúÎßå Ïä§ÌÅ¨Î°§ */
  height: calc(100vh - 70px);
  min-height: 0;
  overflow-y: auto;      /* ‚úÖ Ïù¥ div ÏïàÏóêÏÑú ÏÑ∏Î°ú Ïä§ÌÅ¨Î°§ ÌóàÏö© */
  overflow-x: hidden;
}


/* Í∏∞Ï°¥Ïóê ÏûàÎçò signup-page-containerÎäî z-indexÎßå ÌôïÏã§Ìûà Ï£ºÎ©¥ Îê® */
.signup-page-container {
  position: relative;
  z-index: 10;  /* Î∏îÎûôÌôÄÎ≥¥Îã§ ÏúÑ */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  box-sizing: border-box;
  overflow-y: auto;
}


/* ‚≠ê ÏàòÏ†ï: Ìèº ÏÉÅÏûê ÌÅ¨Í∏∞Î•º LoginÍ≥º Ïú†ÏÇ¨ÌïòÍ≤å Î∞òÏùëÌòïÏúºÎ°ú Ï°∞Ï†ï */
.signup-form-box {
    background: rgba(10, 5, 30, 0.658); 
    border-radius: 16px;
    padding: clamp(25px, 5vw, 40px);
    width: 60%; /* ÌôîÎ©¥ ÎÑàÎπÑÏóê ÎåÄÌïú ÎπÑÏú®ÏùÑ 90%Î°ú ÏàòÏ†ï (ÏõêÎûò 65%ÏóêÏÑú Î≥ÄÍ≤Ω) */
    height: 100%; /* Î∂ÄÎ™® Ïª®ÌÖåÏù¥ÎÑà(.signup-page-container)Ïùò ÎÜíÏù¥Î•º Îî∞Î¶Ñ */
    display: flex;
    flex-direction: column;
    overflow-y: auto; 
}

.signup-form-box h2 {
  text-align: center;
  color: #9a9aff;
  margin-bottom: 30px;
  font-size: 2rem;
  text-shadow: 0 0 6px #7b7bff;
}

/* Ìèº Í∑∏Î£π (Login.cssÏôÄ ÎèôÏùºÌïú Ïä§ÌÉÄÏùºÎ°ú ÌÜµÏùº) */
.form-group {
  margin-bottom: 30px;
}

.signup-form-box form {
    flex: 1; 
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #c9c9ff;
}

.form-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(123, 123, 255, 0.3);
  border-radius: 8px;
  background: rgba(0, 0, 10, 0.7);
  color: #e0e0f8;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group input:focus {
  border-color: #7b7bff;
  box-shadow: 0 0 8px rgba(123, 123, 255, 0.8);
  outline: none;
}

.signup-button {
  width: 100%;
  padding: 14px;
  background: linear-gradient(45deg, #4d4dff, #7b7bff);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  margin-top: 20%;
  transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
  box-shadow: 0 4px 15px rgba(77, 77, 255, 0.4);
  flex-shrink: 0;
}

.signup-button:hover {
  background: linear-gradient(45deg, #6f6fff, #9a9aff);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(77, 77, 255, 0.6);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  font-size: 0.9rem;
  color: #a0a0c8;
}

.login-link a {
  color: #7b7bff;
  text-decoration: none;
  font-weight: bold;
  margin-left: 5px;
  transition: color 0.2s;
}

.login-link a:hover {
  color: #ffffff;
  text-decoration: underline;
}

/* ÏûëÏùÄ ÌôîÎ©¥ (Î™®Î∞îÏùº) ÎåÄÏùë */
@media (max-width: 600px) {
  .signup-form-box {
    padding: 30px 25px;
  }
}

.signup-button:active {
    transform: translateY(0);
    box-shadow: 0 2px 10px rgba(77, 77, 255, 0.8);
}

.login-link a:active {
color: #c9c9ff;
text-decoration: underline;
}

.form-group select {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(123, 123, 255, 0.3);
  border-radius: 8px;
  background: rgba(0, 0, 10, 0.7);
  color: #e0e0f8;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group select:focus {
  border-color: #7b7bff;
  box-shadow: 0 0 8px rgba(123, 123, 255, 0.8);
  outline: none;
}

.email-input-group {
    display: flex;
    align-items: center;
    gap: 8px; /* ÏöîÏÜåÎì§ ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤© */
}

.at-symbol {
    font-size: 1rem;
    color: #c9c9ff;
    font-weight: bold;
    /* ÏÑ∏Î°ú Ï§ëÏïô Ï†ïÎ†¨ÏùÑ ÏúÑÌï¥ padding-topÏùÑ Ï°∞Ï†ïÌï† ÏàòÎèÑ ÏûàÏùå */
}

.email-id-input {
    flex: 4; /* Ï†ÑÏ≤¥ Í≥µÍ∞Ñ Ï§ë 4ÎßåÌÅº Ï∞®ÏßÄ */
    min-width: 0; /* Flexbox ÎÇ¥ÏóêÏÑú ÌÅ¨Í∏∞Í∞Ä Ï§ÑÏñ¥Îì§ÎèÑÎ°ù ÌóàÏö© */
}

.email-domain-input {
    flex: 4; 
}

.email-domain-select {
    flex: 4;
    padding: 12px;
    border: 1px solid rgba(123, 123, 255, 0.3);
    border-radius: 8px;
    background: rgba(0, 0, 10, 0.7);
    color: #e0e0f8;
    font-size: 1rem;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.email-domain-select:focus {
    border-color: #7b7bff;
    box-shadow: 0 0 8px rgba(123, 123, 255, 0.8);
    outline: none;
}

/* ‚úÖ Ïù∏ÎùºÏù∏ ÏóêÎü¨ Î©îÏãúÏßÄ */
.field-error {
  margin-top: 6px;
  font-size: 0.9rem;
  color: #ff6b6b;
  font-weight: 700;
}

/* ‚úÖ Ìèº Ï†ÑÏ≤¥ ÏóêÎü¨ Î©îÏãúÏßÄ */
.form-error {
  margin: 10px 0 6px 0;
  font-size: 0.9rem;
  color: #ff6b6b;
  font-weight: 700;
  text-align: center;
}
