/* manager/M_Main.css - Ìë∏Î•∏Îπõ Îã§ÌÅ¨ ÌÜ§ (ÌÜµÏùº) */

/* Ï†ÑÏó≠ ÏÑ§Ï†ï ÌÜµÏùº */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  /* Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Î∞∞Í≤Ω: #1A222A */
  background-color: #1A222A; 
  color: #ffffff;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden; /* body Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}

#root {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #1A222A;
  overflow: hidden; /* root Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}

/* Ï†ÑÏó≠ Î∞ïÏä§ ÏÇ¨Ïù¥Ïßï */
*, *::before, *::after {
  box-sizing: border-box;
}

.m-container {
  /* Ïª®ÌÖåÏù¥ÎÑà Î∞∞Í≤Ω: #1A222A */
  background-color: #1A222A;
  height: 100vh; /* Ï†ÑÏ≤¥ ÌôîÎ©¥ ÎÜíÏù¥ */
  display: flex;
  flex-direction: column;
  color: white;
  overflow: hidden; /* Ïª®ÌÖåÏù¥ÎÑà ÏûêÏ≤¥ Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}

/* Ìó§ÎçîÎ•º Ï†úÏô∏Ìïú Ïª®ÌÖêÏ∏† ÏòÅÏó≠ */
.m-content-wrapper {
  flex: 1;
  overflow-y: auto; /* Ïù¥ ÏòÅÏó≠Îßå Ïä§ÌÅ¨Î°§ */
  overflow-x: hidden;
  min-height: 0;
}

/* Ïª®ÌÖêÏ∏† ÎûòÌçº Ïä§ÌÅ¨Î°§Î∞î Ïª§Ïä§ÌÑ∞ÎßàÏù¥Ïßï */
.m-content-wrapper::-webkit-scrollbar {
  width: 10px;
}

.m-content-wrapper::-webkit-scrollbar-track {
  background: #1A222A;
}

.m-content-wrapper::-webkit-scrollbar-thumb {
  background: #546A83;
  border-radius: 5px;
}

.m-content-wrapper::-webkit-scrollbar-thumb:hover {
  background: #6A8199;
}

.m-dashboard {
  display: flex;
  gap: 20px;
  padding: 20px;
  flex-wrap: wrap;
  min-height: min-content; /* ÎÇ¥Ïö©Ïóê ÎßûÍ≤å ÎÜíÏù¥ Ï°∞Ï†ï */
}

.m-box {
  /* ÎåÄÏãúÎ≥¥Îìú Î∞ïÏä§ Î∞∞Í≤Ω: #212B36 */
  background-color: #212B36; 
  padding: 20px;
  border-radius: 10px;
  flex: 1;
  min-width: 280px;
  color: white;
  max-height: 500px; /* Î∞ïÏä§ ÏµúÎåÄ ÎÜíÏù¥ */
  overflow-y: auto; /* Î∞ïÏä§ ÎÇ¥ Ïä§ÌÅ¨Î°§ */
  display: flex;
  flex-direction: column;
}

/* Î∞ïÏä§ Ïä§ÌÅ¨Î°§Î∞î Ïª§Ïä§ÌÑ∞ÎßàÏù¥Ïßï */
.m-box::-webkit-scrollbar {
  width: 8px;
}

.m-box::-webkit-scrollbar-track {
  background: #1A222A;
  border-radius: 10px;
}

.m-box::-webkit-scrollbar-thumb {
  background: #546A83;
  border-radius: 10px;
}

.m-box::-webkit-scrollbar-thumb:hover {
  background: #6A8199;
}

.m-box h2 {
  margin: 0 0 15px 0;
  color: white;
  position: sticky;
  top: 0;
  background-color: #212B36;
  padding-bottom: 10px;
  z-index: 1;
  flex-shrink: 0; /* Ï†úÎ™© ÌÅ¨Í∏∞ Í≥†Ï†ï */
}

.m-box-content {
  flex: 1;
  overflow-y: auto; /* Î∞ïÏä§ ÎÇ¥Ïö© Ïä§ÌÅ¨Î°§ */
}

/* Î∞òÏùëÌòï: ÏûëÏùÄ ÌôîÎ©¥ */
@media (max-width: 1024px) {
  .m-dashboard {
    flex-direction: column;
  }
  
  .m-box {
    min-width: 100%;
    max-height: 400px;
  }
}

@media (max-width: 768px) {
  .m-dashboard {
    padding: 15px;
    gap: 15px;
  }
  
  .m-box {
    padding: 15px;
    max-height: 350px;
  }
}

@media (max-width: 480px) {
  .m-dashboard {
    padding: 10px;
  }
  
  .m-box {
    padding: 12px;
    max-height: 300px;
  }
}