/* Introduction.css - Î¨¥Ï±ÑÏÉâ (Monochrome) ÌÜ§ ÏµúÏ¢Ö Î≤ÑÏ†Ñ */

/* ÏÉâÏÉÅ Î≥ÄÏàò (Î¨¥Ï±ÑÏÉâ Í≥ÑÏó¥) */
:root {
    --bg-dark-gray: #202020;         /* ÌéòÏù¥ÏßÄ Î∞∞Í≤Ω (Îã§ÌÅ¨ Í∑∏Î†àÏù¥) */
    --text-primary: #FFFFFF;         /* Í∏∞Î≥∏ Ï†úÎ™© Î∞è Ï£ºÏöî ÌÖçÏä§Ìä∏ (ÏàúÏàò Ìù∞ÏÉâ) */
    --text-secondary: #CCCCCC;       /* Î≥¥Ï°∞ ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
    --container-dark: #2C2C2C;       /* Ïª®ÌÖåÏù¥ÎÑà Î∞è ÏÑπÏÖò Î∞∞Í≤Ω */
    --border-gray: #444444;          /* Íµ¨Î∂ÑÏÑ† Î∞è ÌÖåÎëêÎ¶¨ */
    --accent-monochrome: #AAAAAA;    /* Í∞ïÏ°∞Ïö© ÌöåÏÉâ (ÏïΩÍ∞ÑÏùò Ìè¨Ïù∏Ìä∏) */
    --button-primary: #666666;       /* Î≤ÑÌäº Í∏∞Î≥∏ ÏÉâÏÉÅ */
}

/* 1. Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Î∞è Î†àÏù¥ÏïÑÏõÉ */
.introduction-page-wrapper {
    background-color: var(--bg-dark-gray); 
    /* Ìó§Îçî Ï†úÏô∏ ÏòÅÏó≠Îßå Ï±ÑÏö∞Í≥†, Ïù¥ ÏïàÏóêÏÑúÎßå Ïä§ÌÅ¨Î°§ */
    height: calc(100vh - 70px);
    min-height: 0;
    display: flex;
    flex-direction: column;
    color: var(--text-secondary);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    overflow-y: auto;   /* ‚úÖ ÏÜåÍ∞ú ÌéòÏù¥ÏßÄ Ïä§ÌÅ¨Î°§ Îã¥Îãπ */
    overflow-x: hidden;
}

.introduction-main-content {
    flex-grow: 1;
    padding: 40px 0;
}

.introduction-container {
    width: 90%;
    max-width: 1100px;
    margin: 0 auto;
}

.page-title {
    text-align: center;
    margin-bottom: 50px;
    /* üö® Î¨¥Ï±ÑÏÉâ: ÏàúÏàò Ìù∞ÏÉâÏúºÎ°ú Ï†úÎ™© Í∞ïÏ°∞ */
    color: var(--text-primary); 
    font-size: 2.8em;
    font-weight: 700;
    line-height: 1.4;
    /* ÏùÄÏùÄÌïú Î∞úÍ¥ë Ìö®Í≥º Ï†úÍ±∞ */
    text-shadow: none; 
}

/* 2. ÏÑπÏÖò Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.section-card {
    background-color: var(--container-dark);
    border-radius: 12px;
    padding: 30px;
    margin-bottom: 40px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4);
    /* üö® ÌÖåÎëêÎ¶¨ ÏÉâÏÉÅ ÏàòÏ†ï: Î∞∞Í≤ΩÏÉâÍ≥º ÎπÑÏä∑Ìïú Ïñ¥ÎëêÏö¥ ÌöåÏÉâ */
    border: 1px solid var(--border-gray); 
}

.section-card h2 {
    /* üö® Î¨¥Ï±ÑÏÉâ: Ìù∞ÏÉâÏúºÎ°ú Í∞ïÏ°∞ */
    color: var(--text-primary); 
    font-size: 1.8em;
    margin-bottom: 25px;
    border-bottom: 2px solid var(--border-gray);
    padding-bottom: 10px;
}

/* 3. ÌûàÏñ¥Î°ú ÏÑπÏÖò (ÏÜåÍ∞ú Î¨∏Íµ¨) */
.hero-section {
    display: flex;
    gap: 40px;
    align-items: center;
}

.hero-text {
    flex: 2;
}

.hero-text h2 {
    /* üö® Î¨¥Ï±ÑÏÉâ: Ìù∞ÏÉâÏúºÎ°ú ÌÜµÏùº */
    color: var(--text-primary);
}

.hero-text p {
    font-size: 1.1em;
    line-height: 1.6;
    color: var(--text-secondary); /* Î≥¥Ï°∞ ÌÖçÏä§Ìä∏ */
}

.hero-visual {
    flex: 1;
    min-height: 200px;
    background-color: #3A3A3A; 
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--accent-monochrome);
    font-size: 0.9em;
    border: 1px dashed var(--border-gray);
}

/* 4. Í∏∞Îä• Î™©Î°ù ÏÑπÏÖò */
.feature-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 25px;
}

.feature-item {
    background-color: #333333;
    padding: 20px;
    border-radius: 8px;
    /* üö® Î¨¥Ï±ÑÏÉâ: Ïú†Ï±ÑÏÉâ ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ ÌõÑ ÌöåÏÉâ ÌÖåÎëêÎ¶¨Î°ú ÎåÄÏ≤¥ */
    border-left: 5px solid var(--accent-monochrome);
}

.feature-item h3 {
    color: var(--text-primary);
    margin-top: 0;
    font-size: 1.3em;
    margin-bottom: 10px;
}

.feature-item p {
    color: var(--text-secondary);
}

/* 5. CTA (Call To Action) ÏÑπÏÖò */
.callout-section {
    text-align: center;
    padding: 40px;
}

.callout-section p {
    font-size: 1.1em;
    margin-bottom: 30px;
    color: var(--text-secondary);
}

.cta-button {
    /* primary-btn Ïä§ÌÉÄÏùº ÏÉÅÏÜç */
    padding: 12px 30px;
    font-size: 18px;
    font-weight: 600;
}

/* Î≤ÑÌäº Ïä§ÌÉÄÏùº Ïû¨Ï†ïÏùò (Î¨¥Ï±ÑÏÉâÏóê ÎßûÍ≤å) */
.primary-btn {
    background-color: var(--button-primary); /* ÌöåÏÉâ Î≤ÑÌäº */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.2s;
}

.primary-btn:hover {
    background-color: #555555;
}


/* 6. ÎØ∏ÎîîÏñ¥ ÏøºÎ¶¨ (Î∞òÏùëÌòï) */
@media (max-width: 900px) {
    .page-title {
        font-size: 2.2em;
    }
    .hero-section {
        flex-direction: column;
    }
    .hero-visual {
        width: 100%;
        min-height: 150px;
    }
}

@media (max-width: 600px) {
    .introduction-main-content {
        padding: 20px 0;
    }
    .page-title {
        font-size: 1.8em;
    }
    .section-card {
        padding: 20px;
    }
    .section-card h2 {
        font-size: 1.5em;
    }
}