/* --- Í∏∞Ï°¥ Ïä§ÌÉÄÏùº Ïú†ÏßÄ + ÏàòÏ†ï ÏÇ¨Ìï≠ Ï†ÅÏö© --- */

.header {
  position: sticky;
  top: 0;
  z-index: 1000;
  width: 100%;
  background: rgba(10, 10, 20, 0.7);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px); /* ÏÇ¨ÌååÎ¶¨ Ìò∏ÌôòÏÑ± Ï∂îÍ∞Ä */
  transition: background 0.4s ease;
  border: none;
  box-sizing: border-box;
}

.header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 6vw;
  color: #dcdcdc;
}

/* LOGO */
.logo {
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  font-size: clamp(1.3rem, 2.5vw, 2.2rem);
  font-weight: 800;
  letter-spacing: 1.5px;
  color: #e6ebff;
  text-shadow: 0 0 8px rgba(180, 190, 255, 0.8);
  transition: all 0.4s ease;
  padding-left: 6px;
}

.logo-icon {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: solid 1.5px white;
  object-fit: contain;
}

.logo:hover {
  color: #ffffff;
  transform: scale(1.04);
}

/* RIGHT NAV */
.right-nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.auth-buttons {
  display: flex;
  gap: clamp(10px, 1.5vw, 20px);
  flex-wrap: wrap;
  align-items: center;
}

.auth-buttons button {
  background: none;
  border: none;
  color: #b8bcc5;
  font-size: clamp(0.9rem, 1vw, 1rem);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 8px 12px;
  text-shadow: 0 0 6px rgba(120, 130, 255, 0.3);
}

.auth-buttons button:hover {
  color: #e2e6ef;
  text-shadow: 0 0 16px rgba(160, 180, 255, 0.6);
  transform: translateY(-1px);
}

.logout-btn { opacity: 0.8; }
.logout-btn:hover { opacity: 1; }

/* üîπ ÌñÑÎ≤ÑÍ±∞ Î≤ÑÌäº Ïä§ÌÉÄÏùº (ÏàòÏ†ïÎê®) */
.hamburger-btn {
  /* [Fix] ÎπåÎìú Ïãú z-index Î¨¥Ïãú Î∞©ÏßÄÎ•º ÏúÑÌï¥ position Ï∂îÍ∞Ä */
  position: relative; 
  z-index: 1001; 
  
  background: none;
  border: none;
  cursor: pointer;
  color: #b8bcc5;
  
  /* [Fix] ÏïÑÏù¥ÏΩòÏù¥ Î°úÎìúÎêòÏßÄ ÏïäÏïÑÎèÑ Î≤ÑÌäº ÏòÅÏó≠Ïù¥ ÌôïÎ≥¥ÎêòÎèÑÎ°ù flex Î∞è ÏµúÏÜå ÌÅ¨Í∏∞ ÏÑ§Ï†ï */
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  min-height: 40px;
  
  padding: 4px;
  transition: color 0.3s;
  
  /* [Fix] Í∏∞Î≥∏ Î≤ÑÌäº Ïä§ÌÉÄÏùº Ï¥àÍ∏∞Ìôî Í∞ïÌôî */
  appearance: none;
  -webkit-appearance: none;
}

.hamburger-btn:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(200, 210, 255, 0.9);
}

/* --- üåü ÏÇ¨Ïù¥ÎìúÎ∞î(Drawer) Ïä§ÌÉÄÏùº --- */

/* 1. Î∞∞Í≤Ω Ïò§Î≤ÑÎ†àÏù¥ (Dim Ï≤òÎ¶¨) */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); /* Î∞òÌà¨Î™Ö Í≤ÄÏ†ï */
  backdrop-filter: blur(2px);
  z-index: 1999; /* ÏÇ¨Ïù¥ÎìúÎ∞î Î∞îÎ°ú ÏïÑÎûò */
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.sidebar-overlay.visible {
  opacity: 1;
  visibility: visible;
}

/* 2. ÏÇ¨Ïù¥ÎìúÎ∞î Î©îÎâ¥ Î≥∏Ï≤¥ */
.sidebar-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 280px; /* ÏÇ¨Ïù¥ÎìúÎ∞î ÎÑàÎπÑ */
  height: 100vh;
  background: rgba(15, 15, 30, 0.95); /* ÏßÑÌïú Ïö∞Ï£ºÏÉâ Î∞∞Í≤Ω */
  border-left: 1px solid rgba(120, 130, 255, 0.2);
  box-shadow: -4px 0 20px rgba(0, 0, 0, 0.7);
  z-index: 2000; /* ÏµúÏÉÅÏúÑ Î†àÎ≤® */
  
  /* Ïï†ÎãàÎ©îÏù¥ÏÖò ÌïµÏã¨: Ïò§Î•∏Ï™ΩÏúºÎ°ú Ïà®Í≤®ÎëêÍ∏∞ */
  transform: translateX(100%);
  transition: transform 0.35s cubic-bezier(0.4, 0, 0.2, 1); /* Ï†úÎØ∏ÎÇòÏù¥ ÎäêÎÇåÏùò Î∂ÄÎìúÎü¨Ïö¥ Í∞êÏÜç */
  
  display: flex;
  flex-direction: column;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞îÍ∞Ä Ïó¥Î¶¥ Îïå */
.sidebar-menu.open {
  transform: translateX(0);
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î Ìó§Îçî (Menu ÌÉÄÏù¥ÌãÄ + Îã´Í∏∞ Î≤ÑÌäº) */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-title {
  font-size: 1.2rem;
