}

.close-btn:hover {
  color: #ffffff;
  transform: rotate(90deg); /* ÌöåÏ†Ñ Ìö®Í≥º */
  transition: transform 0.3s ease, color 0.3s;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î ÏΩòÌÖêÏ∏† ÏòÅÏó≠ */
.sidebar-content {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Î©îÎâ¥ ÏïÑÏù¥ÌÖú Ïä§ÌÉÄÏùº */
.menu-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px 16px;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s;
