  transform: translateX(5px); /* ÏÇ¥Ïßù Ïò§Î•∏Ï™ΩÏúºÎ°ú Ïù¥Îèô */
  text-shadow: 0 0 8px rgba(180, 190, 255, 0.6);
}

.menu-icon {
