}

/* Î∞òÏùëÌòï: Î™®Î∞îÏùºÏóêÏÑúÎäî ÌôîÎ©¥ ÍΩâ Ï∞®Í≤å */
@media (max-width: 600px) {
  .header-inner {
    flex-direction: column;
    gap: 10px;
    padding: 0.8rem 4vw;
  }
  .right-nav {
    width: 100%;
    justify-content: center;
  }
  
  /* Î™®Î∞îÏùº ÏÇ¨Ïù¥ÎìúÎ∞î ÎÑàÎπÑ Ï°∞Ï†ï */
  .sidebar-menu {
    width: 75%; /* ÌôîÎ©¥Ïùò 75% Ï∞®ÏßÄ */
    max-width: 300px;
  }
}

/* src/Main.css */
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #000; /* Ï†ÑÏ≤¥ Î∞∞Í≤ΩÏùÑ Í≤ÄÏ†ïÏúºÎ°ú ÏÑ§Ï†ïÌïòÏó¨ Ìù∞ ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ */
  color: #ffffff;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: auto;
}

*, *::before, *::after {
  box-sizing: inherit;
}

.main-wrapper {
  width: 100%;
  min-height: calc(100vh);
  background: #000; /* ÏòµÏÖò A: Îã®ÏÉâ Í≤ÄÏ†ï Î∞∞Í≤Ω */
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

/* Ìó§Îçî ÎÜíÏù¥ÎßåÌÅº Ïó¨Î∞±ÏùÑ Ï§òÏÑú Ìó§ÎçîÏôÄ Í≤πÏπòÏßÄ ÏïäÍ≤å Ìï® (Ìó§ÎçîÍ∞Ä stickyÏù∏ Í≤ΩÏö∞) */
.main-content {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 100px 6vw 60px; /* ÏÉÅÎã® Ìå®Îî©ÏúºÎ°ú Ìó§Îçî Í≥µÍ∞Ñ ÌôïÎ≥¥ */
  text-align: center;
  color: #e6e9ff;
}

/* Ï†úÎ™© ÎÑ§Ïò® ÎäêÎÇå Ïú†ÏßÄ */
.main-content h1 {
  margin: 0 0 10px;
  font-size: clamp(1.4rem, 2.5vw, 2.2rem);
  font-weight: 800;
  color: #e6ebff;
  text-shadow:
    0 0 8px rgba(180, 190, 255, 0.8),
    0 0 20px rgba(150, 170, 255, 0.6);
}
.main-content p {
  margin: 0;
  color: #c9cfff;
  opacity: 0.9;
}

/* Í≥µÌÜµ Ïä§ÌÅ¨Î°§Î∞î - Î¨¥Ï±ÑÏÉâ/Îã§ÌÅ¨ Í∑∏Î†àÏù¥ ÌÜ§ */
.scrollable::-webkit-scrollbar {
  width: 10px;
}

.scrollable::-webkit-scrollbar-track {
  background: #181818;      /* Ïñ¥ÎëêÏö¥ ÌöåÏÉâ (Ìä∏Îûô) */
}

.scrollable::-webkit-scrollbar-thumb {
  background: #d6d6d6;        /* Ï§ëÍ∞Ñ ÌöåÏÉâ (ÏÜêÏû°Ïù¥) */
  border-radius: 5px;
}

.scrollable::-webkit-scrollbar-thumb:hover {
  background: #acacac;        /* Î∞ùÏùÄ ÌöåÏÉâ (Ìò∏Î≤Ñ Ïãú) */
}

/* big star moves to center, zooms there, then returns */
:root { --bg1:#03040c; --bg2:#060a1a; --bg3:#0d1230; }

.space {
  position:fixed;
  inset:0;
  overflow:hidden;
  z-index: 0; /* Î™ÖÏãúÏ†ÅÏúºÎ°ú Î∞∞Í≤Ω z-index ÏÑ§Ï†ï */
}
.layer {
  position:absolute;
  inset:0;
  pointer-events:none;
}
.small-layer {
  pointer-events:none;
  z-index:1;
}
.big-layer {
  pointer-events:auto;
  z-index:2;
}

.warp-layer,
.zoom-layer {
  position:absolute;
  inset:0;
  pointer-events:none;
  overflow:visible;
}
.warp-layer { z-index:5; }
.zoom-layer { z-index:6; }

/* nebula */
.nebula {
  position:absolute;
  inset:-10%;
  z-index:0;
  pointer-events:none;
  background:
    radial-gradient(80vw 60vh at 60% 40%, rgba(200,160,255,0.12) 0%, transparent 70%),
    radial-gradient(50vw 40vh at 20% 70%, rgba(120,200,255,0.10) 0%, transparent 80%),
    radial-gradient(40vw 30vh at 80% 80%, rgba(255,220,180,0.09) 0%, transparent 75%);
  mix-blend-mode:screen;
  animation:nebula-rotate 50s linear infinite;
}
@keyframes nebula-rotate {
  0%   { transform:rotate(0deg); }
  100% { transform:rotate(360deg); }
}

/* stars */
.small-star {
  position:absolute;
  border-radius:999px;
  background:#fff;
  filter:
    drop-shadow(0 0 3px rgba(156,200,255,.9))
    drop-shadow(0 0 6px rgba(156,200,255,.55));
  animation:twinkle 2.5s ease-in-out infinite;
}
@keyframes twinkle {
  0%,100% { opacity:1; }
  50%     { opacity:.7; }
}

/* ===========================
   BIG STAR (Î≥∏Ï≤¥ + ÌéÑÏä§ Î†àÏù¥Ïñ¥ Î∂ÑÎ¶¨)
   =========================== */
.big-star {
  position:absolute;
  width:40px;
  height:40px;
  cursor:pointer;

  /* Î≥Ñ ÎÇ¥Î∂Ä: Ï§ëÏã¨Î∂Ä Î∞ùÍ≤å, Í∞ÄÏû•ÏûêÎ¶¨Îäî ÏÑúÏÑúÌûà ÏÇ¨ÎùºÏßê */
  background:radial-gradient(circle, #ffffff 42%, rgba(255,255,255,0) 70%);

  /* 8Í∞Å Î≥Ñ Î™®Ïñë */
  clip-path:polygon(
    50% 0%,
    60% 40%,
    100% 50%,
    60% 60%,
    50% 100%,
    40% 60%,
    0% 50%,
    40% 40%
  );

  /* Í∏∞Î≥∏ ÎÑ§Ïò® glow */
  filter:
    drop-shadow(0 0 6px rgba(255,235,180,0.9))
    drop-shadow(0 0 18px rgba(255,210,130,0.95))
    drop-shadow(0 0 34px rgba(255,190,90,1));

  transform-origin:center;
  transition:transform .8s ease-out;
}

.big-star::before {
  content:"";
  position:absolute;
  left:50%;
  top:50%;
  width:190%;
  height:190%;
  transform:translate(-50%, -50%) scale(1);
  border-radius:999px;
  pointer-events:none;

  background:radial-gradient(
    circle,
    rgba(255,245,220,0.9) 0%,
    rgba(255,220,150,0.7) 30%,
    rgba(255,180,90,0.35) 55%,
    rgba(255,140,40,0) 80%
  );
  filter:blur(6px);
  opacity:0.9;

  animation:
    big-star-pulse 2.2s ease-in-out infinite,
    big-star-flicker 0.22s steps(2, end) infinite;
}


.big-star::after {
  content:"";
  position:absolute;
  left:50%;
  top:50%;
  width:150%;
  height:150%;
  transform:translate(-50%, -50%);
  border-radius:999px;
  border:1px solid rgba(255,250,220,0.65);
  opacity:0.55;
  filter:blur(1.5px);
  pointer-events:none;
  animation:big-star-halo 4.5s ease-in-out infinite;
}

@keyframes big-star-pulse {
  0% {
    transform:translate(-50%, -50%) scale(0.75);
    opacity:0.5;
    filter:blur(4px);
  }
  40% {
    transform:translate(-50%, -50%) scale(1.35);
    opacity:1;
    filter:blur(9px);
  }
  100% {
    transform:translate(-50%, -50%) scale(0.75);
    opacity:0.5;
    filter:blur(4px);
  }
}

@keyframes big-star-flicker {
  0%, 100% { opacity:1; }
  40%      { opacity:0.9; }
  52%      { opacity:1; }
  70%      { opacity:0.96; }
}

@keyframes big-star-halo {
  0% {
    opacity:0.35;
    transform:translate(-50%, -50%) scale(0.96);
  }
  50% {
    opacity:0.9;
    transform:translate(-50%, -50%) scale(1.1);
  }
  100% {
    opacity:0.35;
    transform:translate(-50%, -50%) scale(0.96);
  }
}

.big-star.zooming {
  animation:star-zoom 1.6s cubic-bezier(.3,1,.3,1) forwards;
}
@keyframes star-zoom {
  0% {
    transform:translate(var(--mx,0), var(--my,0)) scale(1);
    opacity:1;
  }
  40% {
    transform:translate(var(--mx,0), var(--my,0)) scale(3.5);
    opacity:1;
  }
  100% {
    transform:translate(var(--mx,0), var(--my,0)) scale(7);
    opacity:0;
  }
}

.zoom-burst {
  position:absolute;
  width:12px;
  height:12px;
  left:0;
  top:0;
  transform:translate(-50%,-50%) scale(0.1);
  border-radius:999px;
  background:radial-gradient(
    circle,
    rgba(255,255,255,.9) 0%,
    rgba(255,255,255,.6) 14%,
    rgba(255,215,120,.35) 35%,
    rgba(255,215,120,0) 60%
  );
  box-shadow:
    0 0 18px rgba(255,225,150,.8),
    0 0 42px rgba(255,240,180,.6),
    0 0 72px rgba(255,255,255,.35);
  animation:burst-expand .7s ease-out forwards;
}
@keyframes burst-expand {
  0% {
    transform:translate(-50%,-50%) scale(0.1);
    opacity:.95;
  }
  60%{
    transform:translate(-50%,-50%) scale(8);
    opacity:.7;
  }
  100%{
    transform:translate(-50%,-50%) scale(14);
    opacity:0;
  }
}

.streak {
  position:absolute;
  left:0;
  top:0;
  width:var(--len);
  height:1.4px;
  transform-origin:0 50%;
  transform:rotate(var(--angle)) scaleX(0);
  opacity:0;
  filter:
    drop-shadow(0 0 6px rgba(255,255,255,.85))
    drop-shadow(0 0 18px rgba(255,240,180,.8));
  background-size:100% 100%;
  animation:streak var(--dur) ease-out forwards var(--delay);
}
@keyframes streak {
  0% {
    opacity:0;
    transform:rotate(var(--angle)) scaleX(0);
  }
  12% {
    opacity:1;
    transform:rotate(var(--angle)) scaleX(0.22);
  }
  100% {
    opacity:0;
    transform:rotate(var(--angle)) scaleX(1.02);
  }
}

.scene {
  position:absolute;
  inset:0;
  transform-origin:center;
  transition:transform 2.2s ease-in-out;
}
.scene-zooming {
  transform:scale(1.5);
  filter:brightness(1.2) contrast(1.1);
}

.tooltip {
  position:absolute;
  left:0;
  top:0;
  z-index:10;
  pointer-events:none;
  max-width:260px;
  padding:10px 12px;
  border-radius:10px;
  background:rgba(10,14,28,0.65);
  border:1px solid rgba(255,255,255,0.14);
  color:#e8eeff;
  backdrop-filter:blur(8px);
  opacity:0;
  transform:translateY(-4px);
  transition:opacity .18s ease, transform .18s ease;
}
.tooltip.show {
  opacity:1;
  transform:translateY(0);
}
.tooltip .tip-title {
  font-weight:700;
  margin-bottom:4px;
  color:#ffe198;
}
.tooltip .tip-desc {
  font-size:13px;
  line-height:1.4;
  opacity:.92;
}