/* Inquiries.css - Îã§ÌÅ¨ Í∑∏Î†àÏù¥ ÌÜ§ ÏµúÏ¢Ö Î≤ÑÏ†Ñ */

/* Í∏∞Î≥∏ Ïä§ÌÉÄÏùº Î∞è ÏÉâÏÉÅ Î≥ÄÏàò Ï†ïÏùò */
:root {
    --bg-dark-gray: #202020;         /* ÌéòÏù¥ÏßÄ Î∞∞Í≤Ω (Îã§ÌÅ¨ Í∑∏Î†àÏù¥) */
    --text-light: #F0F0F0;           /* Í∏∞Î≥∏ ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
    --container-dark: #2C2C2C;       /* Ïª®ÌÖåÏù¥ÎÑà Î∞è ÌÖåÏù¥Î∏î Î∞∞Í≤Ω */
    --modal-dark: #383838;           /* Î™®Îã¨ Î∞∞Í≤Ω */
    --border-gray: #444444;          /* Íµ¨Î∂ÑÏÑ† Î∞è ÌÖåÎëêÎ¶¨ */
    --primary-blue: #4A90E2;         /* Ï£ºÏöî Î≤ÑÌäº/ÎßÅÌÅ¨ ÏÉâÏÉÅ */
    --success-green: #4A90E2;        /* Ï†úÏ∂ú Î≤ÑÌäº ÏÉâÏÉÅ */
    --secondary-gray: #777777;       /* Ï∑®ÏÜå Î≤ÑÌäº ÏÉâÏÉÅ */
    --header-text-color: #FFFFFF;    /* Í≤åÏãúÌåê Ï†úÎ™© Î∞è ÌÖåÏù¥Î∏î Ìó§Îçî ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
}

/* 1. Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Î†àÏù¥ÏïÑÏõÉ */
.inquiries-page-wrapper {
    background-color: var(--bg-dark-gray); 
    height: calc(100vh - 70px);
    min-height: 0;
    display: flex;
    flex-direction: column;
    color: var(--text-light); 
    overflow-y: auto;   /* ‚úÖ Î¨∏Ïùò Í≤åÏãúÌåê Ïä§ÌÅ¨Î°§ Îã¥Îãπ */
    overflow-x: hidden;
}


.inquiries-main-content {
    flex-grow: 1;
    padding: 20px 0;
}

.inquiries-container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}

.page-title {
    text-align: center;
    margin-bottom: 30px;
    color: var(--header-text-color); /* Í≤åÏãúÌåê ÏÉÅÎã® Ï†úÎ™© */
    font-size: 2em;
    font-weight: 600;
}

/* 2. Í≤ÄÏÉâ Î∞è Ïª®Ìä∏Î°§ ÏòÅÏó≠ */
.controls-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    margin-bottom: 25px;
}

.search-input {
    flex-grow: 1;
    padding: 10px 15px;
    border: 1px solid var(--border-gray);
    border-radius: 6px;
    font-size: 16px;
    background-color: var(--container-dark);
    color: var(--text-light);
    transition: border-color 0.2s, box-shadow 0.2s;
}

.search-input:focus {
    border-color: var(--primary-blue);
    box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.4); 
    outline: none;
}

/* 3. Î≤ÑÌäº Í≥µÌÜµ Ïä§ÌÉÄÏùº Î∞è ÏÉâÏÉÅ */
.primary-btn {
    background-color: var(--primary-blue); /* Î¨∏Ïùò ÏûëÏÑ± Î≤ÑÌäº (Í∏∞Î≥∏ÏÉâ) */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.2s;
}

.primary-btn:hover {
    background-color: #3872b7;
}

/* 4. Í≤åÏãúÌåê ÌÖåÏù¥Î∏î */
.table-wrapper {
    overflow-x: auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    background-color: var(--container-dark);
}

.posts-table {
    width: 100%;
    min-width: 600px;
    border-collapse: collapse;
}

.posts-table th, .posts-table td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid var(--border-gray); 
}

.posts-table th {
    background-color: #333333;
    color: var(--header-text-color); 
    font-weight: 600;
    font-size: 1em;
}

.posts-table tbody tr:hover {
    background-color: #3A3A3A; 
    cursor: pointer;
}

.post-title-cell {
    font-weight: 500;
    /* üåü Í≤åÏãúÍ∏Ä ÎÇ¥Ïö© Ï†úÎ™© ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ: Ìù∞ÏÉâ */
    color: var(--header-text-color); 
}

/* 5. Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥ Î∞è Ìèº */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.85); 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 20px;
}

.inquiry-modal-content {
    background: var(--modal-dark); 
    color: var(--text-light);
    padding: 30px;
    border-radius: 10px;
    width: 95%;
    max-width: 600px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.3s ease-out;
}

.inquiry-modal-content h3 {
    text-align: center;
    margin-top: 0;
    margin-bottom: 25px;
    color: var(--header-text-color); /* Î™®Îã¨ Ï†úÎ™© */
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: var(--text-light); 
}

.form-group input, .form-group textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #555;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 15px;
    background-color: #333333; 
    color: var(--text-light);
}

.form-group textarea {
    resize: vertical;
}

.modal-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 30px;
}

/* Ï†úÏ∂ú Î≤ÑÌäº (ÎÖπÏÉâ Í≥ÑÏó¥, UI ÌÜµÏùº) */
.submit-button {
    background-color: var(--success-green); 
    color: white; 
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.2s;
}

.submit-button:hover {
    background-color: #4CAF50;
}

/* Ï∑®ÏÜå Î≤ÑÌäº (Îã§ÌÅ¨ Í∑∏Î†àÏù¥ Í≥ÑÏó¥, UI ÌÜµÏùº) */
.cancel-button {
    background-color: var(--secondary-gray); 
    color: white; 
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.2s;
}

.cancel-button:hover {
    background-color: #606060;
}


/* 6. ÎØ∏ÎîîÏñ¥ ÏøºÎ¶¨ (Î∞òÏùëÌòï ÎîîÏûêÏù∏) */
@media (max-width: 768px) {
    .inquiries-container {
        width: 95%;
        padding: 0 10px;
    }

    .page-title {
        font-size: 1.8em;
    }
    
    .controls-area {
        flex-direction: column;
        align-items: stretch;
    }

    .search-input, .write-button {
        width: 100%;
        margin-bottom: 10px;
    }

    .posts-table th, .posts-table td {
        padding: 10px;
        font-size: 0.9em;
    }
}