/* ÏßÄÎèÑ ÌéòÏù¥ÏßÄ Ï†ÑÏ≤¥ Ïª®ÌÖåÏù¥ÎÑà */
.map-page-container {
  width: 100%;
  height: calc(100vh - 80px);
  position: relative;
  
  /* CartoDB Light ÏßÄÎèÑÏùò Î∞îÎã§/Î∞∞Í≤ΩÏÉâÍ≥º ÏûêÏó∞Ïä§ÎüΩÍ≤å Ïù¥Ïñ¥ÏßÄÎèÑÎ°ù Î∞ùÏùÄ ÌöåÏÉâÏ°∞ ÏÑ§Ï†ï */
  background-color: #d4dadc;
  
  overflow: hidden;
}

/* Ïã§Ï†ú ÏßÄÎèÑÍ∞Ä Í∑∏Î†§ÏßÄÎäî ÏòÅÏó≠ */
#map {
  width: 100%;
  height: 100%;
  z-index: 0;
  /* ÏßÄÎèÑ Î°úÎî© Ï†Ñ Î∞∞Í≤ΩÏÉâÎèÑ ÎèôÏùºÌïòÍ≤å ÎßûÏ∂§ */
  background: #d4dadc;
}

/* ‚≠êÔ∏è DivIcon (Ïà´Ïûê) Ïä§ÌÉÄÏùºÎßÅ */
.number-icon {
  /* Leaflet Í∏∞Î≥∏ ÎßàÏª§ Ïä§ÌÉÄÏùº ÏÉÅÏÜç Î∞©ÏßÄ */
  border: none !important;
  background: transparent !important;
  
  /* ÏïÑÏù¥ÏΩò ÏòÅÏó≠ÏùÑ Ï§ëÏïô Ï†ïÎ†¨ */
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  
  /* CircleMarkerÏùò Ï§ëÏã¨Í≥º Ï†ïÌôïÌûà Í≤πÏπòÎèÑÎ°ù z-indexÎ•º ÎÜíÏûÖÎãàÎã§. */
  z-index: 700 !important;
  
  /* Ìè¨Ïù∏ÌÑ∞ Ïù¥Î≤§Ìä∏ Î¨¥Ïãú (ÌÅ¥Î¶≠ Ïãú ÏõêÎßå Î∞òÏùëÌïòÎèÑÎ°ù) */
  pointer-events: none;
}

/* Ïà´Ïûê ÌÖçÏä§Ìä∏Î•º Í∞êÏã∏Îäî ÎÇ¥Î∂Ä div Ïä§ÌÉÄÏùºÎßÅ */
.number-icon div {
  font-size: 14px !important;
  font-weight: bold !important;
  color: white !important;
  text-align: center !important;
  line-height: 20px !important;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5) !important;
  user-select: none; /* ÌÖçÏä§Ìä∏ ÏÑ†ÌÉù Î∞©ÏßÄ */
}

/* ‚úÖ ÏßÄÎèÑ Î°úÎî© Ïò§Î≤ÑÎ†àÏù¥ */
.map-loading-overlay {
  position: absolute;
  inset: 0;
  z-index: 9999; /* ÏßÄÎèÑ ÏúÑÎ°ú */
  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(212, 218, 220, 0.75);
  backdrop-filter: blur(2px);
}

/* Î°úÎî© Î∞ïÏä§ */
.map-loading-box {
  min-width: 280px;
  max-width: 420px;
  padding: 18px 20px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.92);
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
  text-align: center;
}

/* Ïä§ÌîºÎÑà */
.map-loading-spinner {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  border: 4px solid rgba(0,0,0,0.12);
  border-top-color: rgba(0,0,0,0.45);
  margin: 0 auto 10px auto;
  animation: mapSpin 0.9s linear infinite;
}

@keyframes mapSpin {
  to { transform: rotate(360deg); }
}

.map-loading-text {
  font-size: 15px;
  font-weight: 700;
  color: rgba(0,0,0,0.78);
  margin-bottom: 6px;
}

.map-loading-subtext {
  font-size: 13px;
  color: rgba(0,0,0,0.55);
  line-height: 1.35;
}

/* Ïû¨ÏãúÎèÑ Î≤ÑÌäº */
.map-loading-retry {
  margin-top: 12px;
  padding: 8px 12px;
  border-radius: 10px;
  border: 1px solid rgba(0,0,0,0.12);
  background: rgba(255,255,255,0.95);
  cursor: pointer;
  font-weight: 700;
}

.map-loading-retry:hover {
  background: rgba(255,255,255,1);
}
